const scrollToAnchor = $ => {
	$('a[href*="#"]').on('click', function (event){
		event.preventDefault();
		const hash = $(this.hash);
		$('html,body').animate({scrollTop: $(this.hash).offset().top}, 500);
	});
};

module.exports = scrollToAnchor;



